 *{
            margin: 0;
            padding: 0;
            box-sizing: border-box;
            font-family: Arial, Helvetica, sans-serif;
        }
        .main{
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100vh;
            background:linear-gradient(to right, #3d3c3e, #cacdd2);
        }
        .container{
            width: 350px;
            background: white;
            padding: 20px;
            border-radius: 10px;
            box-shadow: 0 0 10px rgba(10, 0, 0,0.1);
            text-align: center;
        }
        h2{
            margin-bottom: 20px;
            color: #00000033;

        }
        input{
            width: 100%;
            padding: 10px;
            margin: 10px 0;
            border: 1px solid #ccc;
            border-radius: 5px;
        }
        button{
            width: 100%;
            padding: 10px;
            background:#2575fc;
            border: none;
            color: white;
            border-radius: 5px;
            cursor: pointer;
            margin-top: 10px;

        }
        button:hover{
            background: #6a11cb;
        }
        .toggle{
            margin-top: 15px;
            color: #333;
        }
        .toggle a{
            color: #2575fc;
            text-decoration: none;
            font-weight: bold;

        }
        .toggle a:hover{
            text-decoration: underline;
        }